@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;600;700&display=swap');

.App {
  display: flex;
  flex-direction: column;
  align-items: flex-start;     /* align to the left horizontally */
  justify-content: flex-start; /* align to the top vertically */
  height: 100vh;
  padding-top: 280px;          /* move typing text downward */
  padding-left: 10vw;          /* space from the left edge */
  background-color: #01153E; /* full navy background */
  color: white; /* default text color */
}

.typewriter {
  font-family: 'Raleway', sans-serif;
  font-size: 50px;
  color: white;
}

.cursor {
  display: inline-block;
  width: 2px;
  height: 50px; /* same height as your text */
  background-color: black;
  margin-left: 2px;
  animation: blink 1s step-end infinite;
}

@keyframes blink {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  overflow-x: hidden; /* prevent horizontal scroll */
}

